	module.exports = {"data":{"post":{"html":"<p>My wife's family decided a few years ago to combine all their birthday celebrations into 3 combined gatherings. All birthdays are pretty much clustered in November, February and May/June. So November we go to her older brother, in February to her younger brother, and in June they all come to us. That day is today, and we will have 14 guests in Chateau L체llau.</p>\n<p>We always plan an outdoor celebration, but our party is invariably on the coldest and wettest day of the summer. We're used to putting on sweaters and grilling in the carport. We may have better luck today... the temperature will be cool (18째C) rather than cold, and the showers will be scattered (rather than intense like the past couple of days). Since we have been occupied by other things the past few weeks, we didn't really start preparing until yesterday. </p>\n<p>This year we have a new complication... Christopher came down with the stomach flu overnight, and is still feeling tough this morning. He's generally very healthy, he hasn't been sick for a year and a half. But now we have a child to comfort and extra loads of laundry along with the other preparations. It will be a long day... hopefully we can have *some* fun.</p>","fields":{"title":"Fun Fun Fun","date":"17 Jun 2007","url":"https://papascott.de/2007-06-17-fun-fun-fun/"}}},"pathContext":{"slug":"/2007-06-17-fun-fun-fun/","prev":{"fields":{"title":"Caucasian","date":"17 Jun 2007","slug":"/2007-06-17-caucasian/"}},"next":{"fields":{"title":"24/7","date":"18 Jun 2007","slug":"/2007-06-18-247/"}}}}

